#toast-container {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: 0;
	text-align: center;
	z-index: 9999999999999;
	pointer-events: none;

	&.visible {pointer-events: auto}

	&.visible .message {
		opacity: 1;
		transform: translateY(40px);
	}

	.message {
		display: inline-block;
		padding: 10px 16px;
		background: rgba(0,0,0,.8);
		color: #FFF;
		font-size: 14px;
		font-family: sans-serif;
		border-radius: 3px;

		opacity: 0;
		transform: translateY(-110%);
		transition-duration: .2s;

		a {
			color: #67b9f3;
		}
	}
}